import React, { Component } from 'react';
import Cookies from 'js-cookie';
import Register from './Component/Register';
import ChatList from './Component/ChatList';
import ChatForm from './Component/ChatForm';
import Login from './Component/Login';
import './App.css';

const endpoint = '/api/v1/chatapp/chat/'



class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      chats: [],
      isLoggedIn: !!Cookies.get('Authorization'),
    }
    this.addChats = this.addChats.bind(this);
    this.editChat = this.editChat.bind(this);
    this.removeChat = this.removeChat.bind(this);
    this.handleRegistration = this.handleRegistration.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
    this.handleLogOut = this.handleLogOut.bind(this);
  }

    componentDidMount() {
        fetch(`${endpoint}`)
          .then(res => res.json())
          .then(
            (result) => {
              console.log('response', result)
              this.setState({
                chats: result
              });
            },
            (error) => {
              this.setState({
                error
              });
            }
          )
      }
addChats(chat){
  const chats = [...this.state.chats]
  chats.push(chat);
  this.setState({ chats })
  }

editChat(orgChat, updatedText, id){
  const chats = [...this.state.chats]
  const index = chats.indexOf(orgChat);
  chats[index].text = updatedText;
  this.setState({ chats })
  const text = orgChat.text
  console.log("CHATS", {text}, "ID", id);
  fetch(`${endpoint}update/${id}/`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'X-CSRFToken' : Cookies.get('csrftoken'),
        },
        body: JSON.stringify({text: text}),
      })
        .then(response => {
        if(!response.ok){
          throw new Error ('Bad Post request');
        }
        return response.json()
        })
      .then(data => console.log('Success. ChatApp created!'))
      .catch(error => console.log('Error:', error))
      .finally('I am always going to fire!');
  };



removeChat(chat){
  const chats = [...this.state.chats];
  const index = chats.indexOf(chat);
  chats.splice(index, 1);
  this.setState({ chats });
  fetch(`${endpoint}remove/${chat.id}`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'X-CSRFToken' : Cookies.get('csrftoken'),
        },
        body: JSON.stringify(chats),
      })
        .then(response => {
        if(!response.ok){
          throw new Error ('Bad Post request');
        }
        return response.json()
        })
      .then(data => console.log('Success. ChatApp created!'))
      .catch(error => console.log('Error:', error))
      .finally('I am always going to fire!');
  };

  async handleRegistration(e, obj){
  e.preventDefault();

  const options = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'X-CSRFToken' : Cookies.get('csrftoken'),
    },
    body: JSON.stringify(obj),
  };
  const handleError = (err) => console.warn(err);
  const response = await fetch('/rest-auth/registration/', options);
  const data = await response.json().catch(handleError);
  console.log(data);

  if(data.key){
    Cookies.set('Authorization', `Token ${data.key}`);
    this.setState({isLoggedIn: true})
  }

}
async handleLogin(e, obj){
e.preventDefault();

const options = {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
    'X-CSRFToken' : Cookies.get('csrftoken'),
  },
  body: JSON.stringify(obj),
};
const handleError = (err) => console.warn(err);
const response = await fetch('/rest-auth/login/', options);
const data = await response.json().catch(handleError);
console.log(data);

if(data.key){
  Cookies.set('Authorization', `Token ${data.key}`);
  this.setState({isLoggedIn: true})

}
}

async handleLogOut(e){
e.preventDefault();

alert('logging out');

const options = {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
    'X-CSRFToken' : Cookies.get('csrftoken'),
  },
};
const handleError = (err) => console.warn(err);
const response = await fetch('/rest-auth/logout/', options);
const data = await response.json().catch(handleError);
console.log(data);


  Cookies.remove('Authorization');
  this.setState({isLoggedIn: false})

}

  render(){
      //here is where I need to
    return(
      <React.Fragment>
      <div className="App">
        <Login handleLogin={this.handleLogin} handleLogOut={this.handleLogOut}/>
      <Register handleRegistration={this.handleRegistration} />
      </div>

    { this.state.isLoggedIn === true
      ?
      <div>
      <ChatForm addChats={this.addChats}/>
    <ChatList chats={this.state.chats} editChat={this.editChat}  removeChat={this.removeChat}/>
    </div>
    :
      <div>Not logged in</div>
    }

      </React.Fragment>
    );
  }
}











export default App;


///a function Component is a stateless Component
//class base Component uses state
