import React, { Component } from 'react';

class Login extends Component{
  constructor(props){
    super(props);
    this.state = {
      username: '',
      email: '',
      password: '',
    }
    this.handleInput = this.handleInput.bind(this);
  }

  handleInput(event) {
    this.setState({[event.target.name]: event.target.value});
  }


render(){
  return(
    <>
    <form onSubmit={(e) => this.props.handleLogin(e, this.state)}>
    <input type="text" name="username" value={this.state.username} placeholder="username" onChange={this.handleInput} required/>
    <input type="email" name="email" value={this.state.email} placeholder="email" onChange={this.handleInput} required/>
    <input type="password" name="password" value={this.state.password} placeholder="password" onChange={this.handleInput} required/>
    <button type="submit">Login</button>
    </form>
    <form onSubmit={(e) => this.props.handleLogOut(e, this.state)}>
    <button type="submit">LogOut</button>
    </form>

    </>
  )
}

}


export default Login;
